import React, { Component } from 'react';
import Timeline from "./Timeline";
import TimeInfo from "./TimeInfo";
import allData from "./timeinfo.json"

class History extends Component{    
state = {
    info: "default"
}

displayInfo = (data) => {
    this.setState(
        {
            info: data
        }
    )
}

findData = () => {
    const eventInfo = allData.filter((item) => item.id === this.state.info)
    const data = Object.assign({},eventInfo[0])
    return data
}

render(){
    const data = this.findData()
        return(
        <div className="history">
            <div className="history-body">
                <Timeline 
                    update={this.displayInfo}/>
                <TimeInfo 
                    data={data}
                />
            </div>
            
        </div>
        );
    }
}


export default History
