import React, { Component } from 'react';
import TimeSlot from './TimeSlot'

class Timeline extends Component{    
    render(){
        const { update } =this.props
        return(
            <div className="timeline">
                <TimeSlot 
                    date="June 2021"
                    subject="Functional Programming in JavaScript"
                    update={update}
                    id="jun2021"  />
                <TimeSlot 
                    date="May 2021"
                    subject="Object Oriented JavaScript"
                    update={update}
                    id="may2021" />
                <TimeSlot date="January 2021"
                    subject="React"
                    update={update}
                    id="jan2021" />    
                <TimeSlot date="October 2020"
                    subject="Landing Page Project" 
                    update={update}
                    id="oct2020"/>
                <TimeSlot date="September 2020"
                    subject="Blog Page Website" 
                    update={update}
                    id="sep2020"/>
                <TimeSlot date="Mar-Aug 2020"
                    subject=" Break -" 
                    update={update}
                    id="mar2020"
                    />
                <TimeSlot date="Jan-Feb 2020"
                    subject="Basic HTML/CSS" 
                    
                    id="jan2020"
                    update={update}/>
            </div>
          );
    }
}


export default Timeline