import React, { Component } from 'react';

class TimeSlot extends Component{    
updateTheState    
    render(){
      const { date, update, subject, id } =this.props
      return(
        <div>
          <h3>
            <a onClick = {() => update(id)}>
              {date} - {subject}
            </a>
          </h3>

          <img height="300" width="50"></img>
        </div>
        );
    }
}


export default TimeSlot


